


































































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import { jobsModule, repositoriesModule } from "@/store/modules";
import { Overwrite, EmailNotification } from "@/shared";
import { IJob } from "@/store/modules/JobsModule";
import JobEditor, {
  JobOptionalId,
  getDefaultJob
} from "@/components/misc/JobEditor.vue";
import RepeatPatternInput from "@/components/misc/RepeatPatternInput.vue";
import FileBrowser from "@/components/misc/FileBrowser.vue";
import RepositoryEditor from "@/components/misc/RepositoryEditor.vue";
import { IRepository } from "@/store/modules/RepositoriesModule";
import VueI18n from "vue-i18n";
import { humanReadableExpression } from "@/misc/cron";

@Component({
  components: {
    JobEditor,
    RepeatPatternInput,
    FileBrowser,
    RepositoryEditor
  }
})
export default class JobWizard extends Vue {
  @Prop({
    type: Boolean,
    required: true
  })
  value!: boolean;

  @Watch("jobData.repoId")
  onRepoIdChange() {
    this.step = this.jobData.repoId === -1 ? 2 : 3;
    if (this.step === 2) {
      this.hasRepositoryBeenCreated = false;
    }
  }

  resetJob() {
    this.jobData = getDefaultJob(false);
    this.hasRepositoryBeenCreated = false;
    this.repositoryLoading = false;
    this.$nextTick(() => {
      requestAnimationFrame(() => {
        this.step = 1;
      });
    });
  }

  async onCreateRepository(
    repo: Overwrite<
      IRepository,
      {
        id?: number;
      }
    >
  ) {
    try {
      // TODO: infer args from other class
      const resp = await repositoriesModule.createRepository(repo);
      this.repositoryLoading = true;
      // const resp = { payload: { repo: { id: 999 } } };
      // await new Promise(r => setTimeout(r, 1000));
      if (resp && resp.payload) {
        this.jobData.repoId = resp.payload.repo.id;
        this.hasRepositoryBeenCreated = true;
      }
    } catch (e) {
      console.log(e);
    }
    this.repositoryLoading = false;
  }

  hasRepositoryBeenCreated = false;
  repositoryLoading = false;
  step = 1;

  // repoId = -1 ... create new
  jobData: JobOptionalId = getDefaultJob(false);

  async createJob() {
    // TODO: infer args from other class
    // TODO: trycatch
    const resp = await jobsModule.createJob(this.jobData);
    if (resp && resp.payload) {
      // no need for clearing the form
      this.$router.replace({
        path: `/jobs/${resp.payload.job.id}`
      });
    }
  }

  get open() {
    return this.value;
  }

  set open(val) {
    this.$emit("input", val);
  }

  get repositoriesInChooser() {
    return [
      ...repositoriesModule.allRepositories.map(r => ({
        text: r.name,
        value: r.id
      })),
      {
        text: this.$t("job-wizard.create-new-repository").toString(),
        value: -1
      }
    ];
  }

  nextStep() {
    this.step++;
  }

  get jobIsValid() {
    return (
      this.stepData.selectRepository.complete &&
      this.stepData.jobSettings.complete &&
      this.stepData.fileChooser.complete
    );
  }

  get isRepositoryChosen() {
    // -1 ... create new
    return this.jobData.repoId > 0;
  }

  get stepData() {
    return {
      selectRepository: {
        complete: this.isRepositoryChosen || this.step === 2,
        editable: this.step !== 2
      },
      createRepository: {
        complete: this.hasRepositoryBeenCreated,
        editable: false
      },
      jobSettings: {
        complete: !!this.jobData.name && this.jobData.maxBackups > 0,
        editable: this.isRepositoryChosen
      },
      fileChooser: {
        complete: this.jobData.backupLocations.length > 0,
        editable: this.isRepositoryChosen
      },
      scheduleEditor: {
        complete: this.jobData.active,
        editable: this.isRepositoryChosen
      },
      summary: {
        complete: false,
        editable: this.isRepositoryChosen
      }
    };
  }

  get currentRepoName() {
    return (repositoriesModule.getById(this.jobData.repoId) || { name: "" })
      .name;
  }

  get details(): ({
    title: string | VueI18n.TranslateResult;
    value: string | number | VueI18n.TranslateResult;
  })[] {
    // repeatPattern: string;
    return [
      {
        title: this.$t("job-wizard.details.name"),
        value: this.jobData.name
      },
      {
        title: this.$t("job-wizard.details.repository"),
        value: this.currentRepoName
      },
      {
        title: this.$t("job-wizard.details.max-backups"),
        value: this.jobData.maxBackups
      },
      {
        title: this.$t("job-wizard.details.backup-locations"),
        value: this.jobData.backupLocations.join(", ")
      },
      {
        title: this.$t("job-wizard.details.active"),
        value: this.jobData.active
          ? this.$t("job-wizard.details.is-active")
          : this.$t("job-wizard.details.is-inactive")
      },
      {
        title: this.$t("job-wizard.details.start-date"),
        value: this.$d(this.jobData.startDate, "long")
      },
      {
        title: this.$t("job-wizard.details.repeat-pattern"),
        value: humanReadableExpression(this.jobData.repeatPattern) || ""
      },
      {
        title: this.$t("job-wizard.details.email-notification"),
        value: this.availableEmailNotificationItems.find(
          i => i.value === this.jobData.emailNotification
        )!.text
      }
    ];
  }

  get availableEmailNotificationItems() {
    return [
      {
        text: this.$t("notification-settings.never"),
        value: EmailNotification.never
      },
      {
        text: this.$t("notification-settings.on-error"),
        value: EmailNotification.onerror
      },
      {
        text: this.$t("notification-settings.always"),
        value: EmailNotification.always
      }
    ];
  }
}
