



















































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import { repositoriesModule } from "@/store/modules";
import { Overwrite } from "@/shared";
import { IJob } from "@/store/modules/JobsModule";
import FileBrowser from "@/components/misc/FileBrowser.vue";
import {
  IRepository,
  ISnapshotDetails
} from "@/store/modules/RepositoriesModule";
import VueI18n from "vue-i18n";
import SnapshotFileTree, {
  ISnapshotFileTreeFile,
  dedupeSelectedFiles
} from "@/components/misc/SnapshotFileTree.vue";
import FolderSelectorDialog from "@/components/misc/FolderSelectorDialog.vue";
import { parse } from "date-fns";
import * as api from "@/store/api";

@Component({
  components: {
    FileBrowser,
    SnapshotFileTree,
    FolderSelectorDialog
  }
})
export default class JobWizard extends Vue {
  @Prop({
    type: Boolean,
    required: true
  })
  value!: boolean;

  selectedFiles: string[] = [];

  restoreTargetSelectorOpen = false;
  restoreTarget = "";

  get chosenRepo() {
    return repositoriesModule.getById(this.repoId);
  }

  snapshotsInChooser: { text: string; value: string }[] = [];

  /**
   * 0 ... no repository chosen
   */
  repoId = 0;

  /**
   * 0 ... no repository chosen
   */
  snapshotId = "";

  snapshotDetails: ISnapshotDetails  | false = false;

  @Watch("repoId")
  async onRepoChange() {
    if (!this.repoId) {
      return (this.snapshotsInChooser = []);
    }
    this.step++;
    this.snapshotId = "";
    this.snapshotsInChooser = (await repositoriesModule.listSnapshots(
      this.repoId
    )).map(s => ({
      text: `${s.id} - ${this.$d(s.date, "long")}`,
      value: s.id
    }));
  }

  @Watch("snapshotId")
  async onSnapshotChange() {
    this.selectedFiles = [];
    if (!this.repoId || !this.snapshotId) {
      return (this.snapshotDetails = false);
    }
    this.step++;
    this.snapshotDetails = await repositoriesModule.getSnapshotDetails({
      repoId: this.repoId,
      snapshotId: this.snapshotId
    });
  }

  resetWizard() {
    this.repoId = 0;
    this.snapshotId = "";
    this.restoreTarget = "";
    this.$nextTick(() => {
      requestAnimationFrame(() => {
        this.step = 1;
      });
    });
  }

  step = 1;

  async restoreNow() {
    await repositoriesModule.restoreSnapshot({
      repoId: this.repoId,
      args: {
        restorePath: this.restoreTarget,
        selectedPaths: this.selectedFiles,
        snapshotId: this.snapshotId
      }
    });
    this.selectedFiles = [];
  }

  get open() {
    return this.value;
  }

  set open(val) {
    this.$emit("input", val);
  }

  get valuesValid() {
    return (
      this.repoId &&
      this.snapshotId &&
      this.selectedFiles &&
      this.selectedFiles.length &&
      this.restoreTarget
    );
  }

  nextStep() {
    this.step++;
  }

  get stepData() {
    return {
      chooseRepository: {
        complete: !!this.repoId,
        editable: true
      },
      chooseSnapshot: {
        complete: !!this.snapshotId,
        editable: !!this.repoId
      },
      chooseFolders: {
        complete: this.selectedFiles.length > 0,
        editable: !!(this.snapshotId && this.repoId)
      },
      chooseDestination: {
        complete: !!this.restoreTarget,
        editable: true
      },
      summary: {
        complete: false,
        editable: true
      }
    };
  }

  get repositoriesInChooser() {
    return [
      ...repositoriesModule.allRepositories.map(r => ({
        text: r.name,
        value: r.id
      }))
    ];
  }

  get canGoToNextStep() {
    return (
      (Number(this.step) === 1 && this.repoId) ||
      (Number(this.step) === 2 && this.snapshotId) ||
      (Number(this.step) === 3 &&
        this.selectedFiles &&
        this.selectedFiles.length) ||
      (Number(this.step) === 4 && !!this.restoreTarget) ||
      (Number(this.step) === 5 && this.valuesValid)
    );
  }
}
